<template>
  <div class="profile">
    <header class="header">
      <div class="container">
        <div class="header-nav">
          <RouterLink to="/" class="back-link">‚Üê ËøîÂõûÈ¶ñÈ°µ</RouterLink>
          <nav class="nav" v-if="!isViewingOtherUser">
            <RouterLink to="/profile" class="nav-link active">‰∏™‰∫∫‰∏≠ÂøÉ</RouterLink>
            <RouterLink to="/bookmarks" class="nav-link">
              <i class="bi bi-bookmark"></i>
              Êî∂Ëóè
            </RouterLink>
            <RouterLink to="/messages" class="nav-link">
              <i class="bi bi-envelope"></i>
              Ê∂àÊÅØ
            </RouterLink>
          </nav>
        </div>
        <h1>{{ isViewingOtherUser ? 'Áî®Êà∑ËµÑÊñô' : '‰∏™‰∫∫‰∏≠ÂøÉ' }}</h1>
      </div>
    </header>

    <main class="main">
      <div class="container">
        <div v-if="loading" class="loading">Âä†ËΩΩ‰∏≠...</div>
        
        <div v-else-if="profile" class="profile-content">
          <!-- Áî®Êà∑‰ø°ÊÅØÂç°Áâá -->
          <div class="profile-card">
            <div class="profile-header">
              <div class="avatar-container" @click="!isViewingOtherUser && canChangeAvatar && (showAvatarSelector = true)">
                <UserAvatar 
                  :username="profile.username" 
                  :avatar-id="profile.avatar_url"
                  size="80px"
                />
                <div class="avatar-edit-hint" v-if="!isViewingOtherUser && canChangeAvatar">
                  <span>ÁÇπÂáªÊõ¥Êç¢</span>
                </div>
                <div class="avatar-locked" v-else-if="!isViewingOtherUser">
                  <span>Lv.3 Ëß£ÈîÅ</span>
                </div>
              </div>
              <div class="profile-info">
                <h2 class="username" :class="getLevelClass(profile.level)">
                  {{ profile.username }}
                  <span class="level-badge">Lv.{{ profile.level }} {{ getLevelName(profile.level) }}</span>
                </h2>
                <p class="member-since">
                  Ê≥®ÂÜåÊó∂Èó¥Ôºö{{ formatDate(profile.created_at) }}
                </p>
              </div>
            </div>

            <!-- ÁªèÈ™åÂÄºËøõÂ∫¶Êù° -->
            <div class="experience-section">
              <div class="exp-info">
                <span>ÁªèÈ™åÂÄºÔºö{{ profile.experience_points || 0 }}</span>
                <span>‰∏ã‰∏ÄÁ≠âÁ∫ßÔºö{{ (nextLevelExp - (profile.experience_points || 0)) > 0 ? (nextLevelExp - (profile.experience_points || 0)) + ' EXP' : 'Â∑≤Êª°' }}</span>
                <button @click="debugExperience" class="debug-btn" title="Ë∞ÉËØïÁªèÈ™åÂÄº">
                  Ë∞ÉËØï
                </button>
              </div>
              <div class="exp-progress">
                <div 
                  class="exp-bar" 
                  :style="{ width: expProgress + '%' }"
                ></div>
                <span class="exp-text">{{ expProgress }}%</span>
              </div>
              <div class="exp-debug">
                <button @click="addTestExperience" class="test-btn">
                  ÊµãËØï+10ÁªèÈ™å
                </button>
              </div>
            </div>

            <!-- Áî®Êà∑ÁâπÊùÉ -->
            <div class="privileges-section">
              <h3>ÂΩìÂâçÁâπÊùÉ</h3>
              <div class="privileges-list">
                <div 
                  v-for="privilege in currentPrivileges" 
                  :key="privilege.id"
                  class="privilege-item"
                >
                  <span class="privilege-icon">{{ privilege.icon }}</span>
                  <span class="privilege-name">{{ privilege.name }}</span>
                </div>
              </div>
            </div>
          </div>

          <!-- Áî®Êà∑Â∏ñÂ≠ê -->
          <div class="user-posts">
            <h3>ÊàëÁöÑÂ∏ñÂ≠ê</h3>
            <div v-if="userPosts.length > 0" class="posts-list">
              <PostCard 
                v-for="post in userPosts" 
                :key="post.id" 
                :post="post"
                @like="handleLike"
                @comment="handleComment"
              />
            </div>
            <div v-else class="no-posts">
              <p>ËøòÊ≤°ÊúâÂèëÂ∏ÉËøáÂ∏ñÂ≠ê</p>
              <RouterLink to="/" class="btn-primary">ÂéªÂèëÂ∏ÉÁ¨¨‰∏Ä‰∏™Â∏ñÂ≠ê</RouterLink>
            </div>
          </div>
        </div>
        
        <div v-else class="error">
          <p>Áî®Êà∑‰ø°ÊÅØÂä†ËΩΩÂ§±Ë¥•</p>
        </div>
      </div>
    </main>

    <!-- Â§¥ÂÉèÈÄâÊã©Âô®Ê®°ÊÄÅÊ°Ü -->
    <div v-if="showAvatarSelector" class="modal-overlay" @click.self="showAvatarSelector = false">
      <div class="modal-content">
        <AvatarSelector 
          @close="showAvatarSelector = false"
          @select="handleAvatarSelect"
        />
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, computed } from 'vue'
import { useRoute } from 'vue-router'
import { useAuthStore } from '@/stores/auth'
import { usePostStore } from '@/stores/posts'
import PostCard from '@/components/PostCard.vue'
import UserAvatar from '@/components/UserAvatar.vue'
import AvatarSelector from '@/components/AvatarSelector.vue'
import type { Database } from '@/types/supabase'

const route = useRoute()
const authStore = useAuthStore()
const postStore = usePostStore()

// Ê£ÄÊü•ÊòØÂê¶ÊòØÊü•ÁúãÂÖ∂‰ªñÁî®Êà∑ÁöÑËµÑÊñô
const isViewingOtherUser = computed(() => !!route.params.id)
const targetUserId = computed(() => route.params.id as string)

const loading = ref(true)
const userPosts = ref<Database['public']['Tables']['posts']['Row'][]>([])
const showAvatarSelector = ref(false)

const profile = computed(() => {
  if (isViewingOtherUser.value) {
    return otherUserProfile.value
  }
  return authStore.profile
})

const otherUserProfile = ref<any>(null)

// ËÆ∫ÂùõÁ≠âÁ∫ßÁªèÈ™åË°®
const getLevelExpRequired = (level: number) => {
  const expTable = [
    0,    // Lv.1
    50,   // Lv.2 - Êñ∞Êâã
    150,  // Lv.3 - ÂàùÁ∫ß
    300,  // Lv.4 - ‰∏≠Á∫ß
    500,  // Lv.5 - È´òÁ∫ß
    800,  // Lv.6 - ËµÑÊ∑±
    1200, // Lv.7 - ‰∏ìÂÆ∂
    1800, // Lv.8 - Â§ßÂ∏à
    2500, // Lv.9 - ÂÆóÂ∏à
    3500, // Lv.10 - ‰º†Â•á
    5000, // Lv.11 - Âè≤ËØó
    7000, // Lv.12 - Á•ûËØù
    10000 // Lv.13 - Ëá≥Â∞ä
  ]
  
  if (level <= 1) return 0
  if (level >= expTable.length) {
    // Ë∂ÖËøáË°®Ê†ºËåÉÂõ¥Ôºå‰ΩøÁî®ÈÄíÂ¢ûÂÖ¨Âºè
    const baseExp = expTable[expTable.length - 1]
    const extraLevels = level - expTable.length + 1
    return baseExp + extraLevels * 2000
  }
  
  return expTable[level - 1]
}

// ËÆ°ÁÆó‰∏ã‰∏ÄÁ≠âÁ∫ßÊâÄÈúÄÁªèÈ™åÂÄº
const nextLevelExp = computed(() => {
  if (!profile.value) return 50
  const level = profile.value.level
  return getLevelExpRequired(level + 1)
})

// ËÆ°ÁÆóÂΩìÂâçÁ≠âÁ∫ßÊâÄÈúÄÁªèÈ™åÂÄº
const currentLevelExp = computed(() => {
  if (!profile.value) return 0
  const level = profile.value.level
  return getLevelExpRequired(level)
})

// ËÆ°ÁÆóÁªèÈ™åÂÄºËøõÂ∫¶
const expProgress = computed(() => {
  if (!profile.value) return 0
  
  const currentExp = profile.value.experience_points || 0
  const currentLevelReq = currentLevelExp.value
  const nextLevelReq = nextLevelExp.value
  
  console.log('ËÆ°ÁÆóÁªèÈ™åËøõÂ∫¶:', {
    currentExp,
    currentLevelReq,
    nextLevelReq,
    level: profile.value.level
  })
  
  // Â¶ÇÊûúÂ∑≤ÁªèËææÂà∞ÊàñË∂ÖËøá‰∏ã‰∏ÄÁ≠âÁ∫ßË¶ÅÊ±ÇÔºåÊòæÁ§∫100%
  if (currentExp >= nextLevelReq) return 100
  
  // ÊåâÁÖß‰Ω†ÁöÑË¶ÅÊ±ÇÔºöÁªèÈ™åÂÄº / (ÁªèÈ™åÂÄº + Âà∞‰∏ã‰∏ÄÁ≠âÁ∫ßÊâÄÈúÄÁªèÈ™åÂÄº)
  const expNeededForNext = nextLevelReq - currentExp
  const totalExpForProgress = currentExp + expNeededForNext
  
  console.log('ËøõÂ∫¶ËÆ°ÁÆó:', {
    expNeededForNext,
    totalExpForProgress
  })
  
  if (totalExpForProgress <= 0) return 0
  
  // Êñ∞ÁöÑËøõÂ∫¶ËÆ°ÁÆóÔºöÂΩìÂâçÁªèÈ™åÂÄº / (ÂΩìÂâçÁªèÈ™åÂÄº + Âà∞‰∏ã‰∏ÄÁ≠âÁ∫ßÊâÄÈúÄÁªèÈ™åÂÄº)
  const progress = Math.floor((currentExp / totalExpForProgress) * 100)
  
  console.log('ÊúÄÁªàËøõÂ∫¶:', progress)
  
  // Á°Æ‰øùËøõÂ∫¶Âú®0-100‰πãÈó¥
  return Math.max(0, Math.min(100, progress))
})

// Ëé∑ÂèñÁ≠âÁ∫ßÂêçÁß∞
const getLevelName = (level: number) => {
  const levelNames = [
    'Êñ∞Êâã', 'ÂàùÁ∫ß', '‰∏≠Á∫ß', 'È´òÁ∫ß', 'ËµÑÊ∑±',
    '‰∏ìÂÆ∂', 'Â§ßÂ∏à', 'ÂÆóÂ∏à', '‰º†Â•á', 'Âè≤ËØó',
    'Á•ûËØù', 'Ëá≥Â∞ä'
  ]
  
  if (level <= 1) return 'Êñ∞Êâã'
  if (level > levelNames.length) return 'Ëá≥Â∞ä'
  return levelNames[level - 1]
}

// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Êõ¥Êç¢Â§¥ÂÉè
const canChangeAvatar = computed(() => {
  const level = profile.value?.level || 1
  return level >= 3
})

// ÂΩìÂâçÁâπÊùÉÂàóË°®
const currentPrivileges = computed(() => {
  const level = profile.value?.level || 1
  const privileges = []

  // Âü∫Á°ÄÁâπÊùÉ
  privileges.push({ id: 'basic', name: 'ÂèëÂ∏ñ„ÄÅËØÑËÆ∫„ÄÅÁÇπËµû', icon: 'üìù' })

  if (level >= 3) {
    privileges.push({ id: 'avatar', name: 'Ëá™ÂÆö‰πâÂ§¥ÂÉè', icon: 'üñºÔ∏è' })
  }

  if (level >= 5) {
    privileges.push({ id: 'signature', name: '‰∏™ÊÄßÁ≠æÂêç', icon: '‚úçÔ∏è' })
  }

  if (level >= 7) {
    privileges.push({ id: 'color', name: '‰∏™ÊÄßÂåñËâ≤ÂΩ©', icon: 'üé®' })
  }

  if (level >= 10) {
    privileges.push({ id: 'mod', name: 'ËØÑËÆ∫ÁΩÆÈ°∂ÊùÉÈôê', icon: '‚≠ê' })
  }

  return privileges
})

onMounted(async () => {
  await loadUserData()
})

const loadUserData = async () => {
  console.log('ÂºÄÂßãÂä†ËΩΩÁî®Êà∑Êï∞ÊçÆ...')
  loading.value = true
  
  try {
    if (isViewingOtherUser.value) {
      // Âä†ËΩΩÂÖ∂‰ªñÁî®Êà∑ÁöÑËµÑÊñô
      console.log('Âä†ËΩΩÂÖ∂‰ªñÁî®Êà∑ËµÑÊñôÔºåID:', targetUserId.value)
      await loadOtherUserData()
    } else {
      // Âä†ËΩΩÂΩìÂâçÁî®Êà∑ÁöÑËµÑÊñô
      // Á°Æ‰øùÁî®Êà∑Â∑≤ÁôªÂΩï
      if (!authStore.user) {
        console.warn('Áî®Êà∑Êú™ÁôªÂΩïÔºåÊó†Ê≥ïÂä†ËΩΩ‰∏™‰∫∫ËµÑÊñô')
        loading.value = false
        return
      }
      
      console.log('Áî®Êà∑Â∑≤ÁôªÂΩïÔºåID:', authStore.user.id)
      
      // Âä†ËΩΩÁî®Êà∑ËµÑÊñôÔºåÂ¶ÇÊûúÂ§±Ë¥•Âàô‰ΩøÁî®ÈªòËÆ§ÂÄº
      if (!authStore.profile) {
        console.log('Áî®Êà∑ËµÑÊñô‰∏∫Á©∫ÔºåÂ∞ùËØïËé∑Âèñ...')
        try {
          console.log('Ë∞ÉÁî®fetchProfile...')
          await authStore.fetchProfile()
          console.log('fetchProfileÂÆåÊàê')
        } catch (profileError) {
          console.warn('Âä†ËΩΩÁî®Êà∑ËµÑÊñôÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂÄº:', profileError)
          // ‰∏çÊäõÂá∫ÈîôËØØÔºåÁªßÁª≠Âä†ËΩΩÁî®Êà∑Â∏ñÂ≠ê
        }
      } else {
        console.log('Áî®Êà∑ËµÑÊñôÂ∑≤Â≠òÂú®:', authStore.profile)
      }
      
      // Âä†ËΩΩÁî®Êà∑Â∏ñÂ≠ê
      console.log('ÂºÄÂßãÂä†ËΩΩÁî®Êà∑Â∏ñÂ≠ê...')
      await loadUserPosts()
      console.log('Áî®Êà∑Â∏ñÂ≠êÂä†ËΩΩÂÆåÊàê')
    }
  } catch (error) {
    console.error('Âä†ËΩΩÁî®Êà∑Êï∞ÊçÆÂ§±Ë¥•:', error)
  } finally {
    // Á°Æ‰øùloadingÁä∂ÊÄÅÊÄªÊòØË¢´ËÆæÁΩÆ‰∏∫false
    console.log('ËÆæÁΩÆloading‰∏∫false')
    loading.value = false
  }
}

const loadUserPosts = async () => {
  if (!authStore.user) {
    console.warn('Áî®Êà∑Êú™ÁôªÂΩïÔºåÊó†Ê≥ïÂä†ËΩΩÁî®Êà∑Â∏ñÂ≠ê')
    userPosts.value = []
    return
  }
  
  try {
    const result = await postStore.fetchUserPosts(authStore.user.id)
    if (result.success) {
      userPosts.value = result.data || []
    } else {
      console.error('Âä†ËΩΩÁî®Êà∑Â∏ñÂ≠êÂ§±Ë¥•:', result.error)
      userPosts.value = []
    }
  } catch (error) {
    console.error('Âä†ËΩΩÁî®Êà∑Â∏ñÂ≠êÂ§±Ë¥•:', error)
    userPosts.value = []
  }
}

const handleLike = (postId: string) => {
  console.log('ÁÇπËµûÂ∏ñÂ≠ê:', postId)
}

const handleComment = (postId: string) => {
  console.log('ËØÑËÆ∫Â∏ñÂ≠ê:', postId)
}

const handleAvatarSelect = async (avatar: any) => {
  try {
    // ÂÖàÂÖ≥Èó≠Ê®°ÊÄÅÊ°ÜÔºåÈÅøÂÖçDOMÊìç‰ΩúÂÜ≤Á™Å
    showAvatarSelector.value = false
    
    // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
    localStorage.setItem('userAvatar', avatar.id)
    
    // Êõ¥Êñ∞Êú¨Âú∞Áä∂ÊÄÅ
    if (profile.value) {
      profile.value.avatar_url = avatar.id
    }
    
    // Êõ¥Êñ∞Âà∞Êï∞ÊçÆÂ∫ì
    const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
    const supabaseKey = import.meta.env.VITE_SUPABASE_ANON_KEY
    
    if (!supabaseUrl || !supabaseKey || 
        supabaseUrl.includes('default.supabase.co') || 
        supabaseKey.includes('default')) {
      // ÂºÄÂèëÊ®°Âºè‰∏ãÂè™Êõ¥Êñ∞Êú¨Âú∞Áä∂ÊÄÅ
      console.log('ÂºÄÂèëÊ®°ÂºèÔºöÂ§¥ÂÉèÂ∑≤Êõ¥Êñ∞Âà∞Êú¨Âú∞Áä∂ÊÄÅ')
      return
    }
    
    // Áîü‰∫ßÁéØÂ¢É‰∏ãÂêåÊ≠•Âà∞Êï∞ÊçÆÂ∫ì
    const { supabase } = await import('@/services/supabase')
    const { error } = await supabase
      .from('profiles')
      .update({
        avatar_url: avatar.id,
        updated_at: new Date().toISOString()
      })
      .eq('id', authStore.user?.id)
    
    if (error) {
      console.error('Êõ¥Êñ∞Â§¥ÂÉèÂ§±Ë¥•:', error)
      // ÂõûÊªöÊú¨Âú∞Áä∂ÊÄÅ
      const previousAvatar = localStorage.getItem('userAvatar')
      if (profile.value) {
        profile.value.avatar_url = previousAvatar || null
      }
    } else {
      console.log('Â§¥ÂÉèÊõ¥Êñ∞ÊàêÂäü')
    }
  } catch (error) {
    console.error('Êõ¥Êñ∞Â§¥ÂÉèÂ§±Ë¥•:', error)
  }
}

// Âä†ËΩΩÂÖ∂‰ªñÁî®Êà∑Êï∞ÊçÆ
const loadOtherUserData = async () => {
  try {
    const { supabase } = await import('@/services/supabase')
    
    // Ëé∑ÂèñÁî®Êà∑ËµÑÊñô
    const { data: profileData, error: profileError } = await supabase
      .from('profiles')
      .select('*')
      .eq('id', targetUserId.value)
      .single()
    
    if (profileError) {
      console.error('Ëé∑ÂèñÁî®Êà∑ËµÑÊñôÂ§±Ë¥•:', profileError)
      loading.value = false
      return
    }
    
    otherUserProfile.value = profileData
    console.log('ÂÖ∂‰ªñÁî®Êà∑ËµÑÊñôÂä†ËΩΩÊàêÂäü:', profileData)
    
    // Ëé∑ÂèñÁî®Êà∑Â∏ñÂ≠ê
    const { data: postsData, error: postsError } = await supabase
      .from('posts')
      .select('*')
      .eq('user_id', targetUserId.value)
      .order('created_at', { ascending: false })
    
    if (postsError) {
      console.error('Ëé∑ÂèñÁî®Êà∑Â∏ñÂ≠êÂ§±Ë¥•:', postsError)
    } else {
      userPosts.value = postsData || []
    }
    
  } catch (error) {
    console.error('Âä†ËΩΩÂÖ∂‰ªñÁî®Êà∑Êï∞ÊçÆÂ§±Ë¥•:', error)
  } finally {
    loading.value = false
  }
}

// Â∑•ÂÖ∑ÂáΩÊï∞
const getLevelClass = (level: number) => {
  if (level >= 10) return 'level-10-plus'
  if (level >= 7) return 'level-7-9'
  if (level >= 4) return 'level-4-6'
  return 'level-1-3'
}

const formatTime = (timestamp: string) => {
  const date = new Date(timestamp)
  const now = new Date()
  const diff = now.getTime() - date.getTime()
  
  if (diff < 60000) return 'ÂàöÂàö'
  if (diff < 3600000) return `${Math.floor(diff / 60000)}ÂàÜÈíüÂâç`
  if (diff < 86400000) return `${Math.floor(diff / 3600000)}Â∞èÊó∂Ââç`
  if (diff < 604800000) return `${Math.floor(diff / 86400000)}Â§©Ââç`
  
  return date.toLocaleDateString('zh-CN')
}

// ‰∏¥Êó∂Ë∞ÉËØïÂáΩÊï∞
const debugExperience = () => {
  if (profile.value) {
    console.log('Profile data:', profile.value)
    console.log('Experience points:', profile.value.experience_points)
    console.log('Level:', profile.value.level)
    console.log('Current level exp:', currentLevelExp.value)
    console.log('Next level exp:', nextLevelExp.value)
    console.log('Progress:', expProgress.value)
  }
}

// ÊµãËØïÊ∑ªÂä†ÁªèÈ™åÂÄº
const addTestExperience = async () => {
  await authStore.updateExperience(10)
  debugExperience()
}

const formatDate = (timestamp: string) => {
  if (!timestamp) return 'Êú™Áü•Êó∂Èó¥'
  return new Date(timestamp).toLocaleDateString('zh-CN')
}
</script>

<style scoped>
